@media screen and (min-width: 992px) {
/* Ne montrer que pour la première ligne large, marquée par JS */
.watches_gallery_item-wrap.is-feature-line > .watches_gallery_txt-wrap.is-title {
  display: flex !important;
}
.watches_gallery_img,
.watches_gallery_txt-wrap:not(.is-title) {
  transition: opacity 280ms ease;
}
.watches_gallery_item:hover .watches_gallery_img,
.watches_gallery_item:hover .watches_gallery_txt-wrap:not(.is-title) {
  opacity: 1;
}
.watches_gallery_item.is-hover .watches_gallery_img,
.watches_gallery_item.is-hover .watches_gallery_txt-wrap:not(.is-title) {
  opacity: 1;
}

/* GALLERY- Pattern 2-1-2-1... */
.watches_gallery {
  display: grid !important;
  grid-template-columns: repeat(2, 1fr) !important;
  gap: 120px !important;
  grid-auto-flow: row dense !important;
}
.watches_gallery > .watches_gallery_item-wrap {
  grid-column: span 1 !important;
}
.watches_gallery > .watches_gallery_item-wrap:nth-child(3n) {
  grid-column: 1 / -1 !important;
}
.watches_gallery > .watches_gallery_item-wrap:nth-child(3n) .watches_gallery_img-wrap.is-empty {
  display: block !important;
}
/* Sur chaque 3e item (ligne large): l'item s'étend sur les 2 colonnes et crée sa propre grille interne avec un gap plus petit */
.watches_gallery > .watches_gallery_item-wrap:nth-child(3n) .watches_gallery_item {
  display: flex !important;
}
}


/* GALLERY + GRID - Base overlay setup (desktop only) */
@media (min-width: 992px) {
  .watches_gallery_img-wrap:not([class*=' ']):not([class*='is-']),
  .watches_grid_img-wrap:not([class*=' ']):not([class*='is-']) {
    position: relative;
    transition: filter 280ms ease;
  }

  .watches_gallery_img-wrap:not([class*=' ']):not([class*='is-'])::before,
  .watches_grid_img-wrap:not([class*=' ']):not([class*='is-'])::before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(0deg, var(--filter-dark-large, rgba(22, 21, 29, 0.75)) 0%, var(--filter-dark-large, rgba(22, 21, 29, 0.75)) 100%);
    opacity: 0;
    pointer-events: none;
    transition: opacity 280ms ease;
    z-index: 1;
  }
}

/* GALLERY - Hover effects (desktop only) */
@media (min-width: 992px) {
  .watches_gallery:has(.watches_gallery_item:hover) .watches_gallery_item:not(:hover) .watches_gallery_img-wrap:not([class*=' ']):not([class*='is-'])::before {
    opacity: 1;
  }

  .watches_gallery:has(.watches_gallery_item:hover) .watches_gallery_item:not(:hover) .watches_gallery_img-wrap:not([class*=' ']):not([class*='is-']) {
    filter: blur(calc(var(--blur-small, 4px) / 2));
  }
}

/* GRID - Hover effects (desktop only) */
@media (min-width: 992px) {
  .watches_grid_card .watches_gallery_img,
  .watches_grid_card .watches_grid_text-wrap {
    transition: opacity 280ms ease;
  }

  .watches_grid_card:hover .watches_gallery_img,
  .watches_grid_card:hover .watches_grid_text-wrap {
    opacity: 1;
  }

  .watches_grid:has(.watches_grid_card:hover) .watches_grid_card:not(:hover) .watches_grid_img-wrap:not([class*=' ']):not([class*='is-'])::before {
    opacity: 1;
  }

  .watches_grid:has(.watches_grid_card:hover) .watches_grid_card:not(:hover) .watches_grid_img-wrap:not([class*=' ']):not([class*='is-']) {
    filter: blur(calc(var(--blur-small, 4px) / 2));
  }
}

/* VIEW TOGGLE - smooth reveal/hide for gallery/grid wrappers */
.watches_gallery_wrapper,
.watches_grid_wrapper {
  overflow: hidden;
  transition:
    height 320ms ease,
    opacity 220ms ease;
}
.watches_gallery_wrapper.is-hidden,
.watches_grid_wrapper.is-hidden {
  height: 0 !important; /* override Webflow inline/cls if any */
  opacity: 0;
  pointer-events: none;
}
.watches_gallery_wrapper.is-visible,
.watches_grid_wrapper.is-visible {
  height: auto !important;
  opacity: 1;
}

/* IN-VIEW FADE-UP for rows (gallery + grid) */
.watches_gallery_item-wrap,
.watches_grid_card {
  opacity: 0;
  transform: translateY(12px);
  transition:
    opacity 420ms ease,
    transform 420ms ease;
}
.watches_gallery_item-wrap.is-inview,
.watches_grid_card.is-inview {
  opacity: 1;
  transform: translateY(0);
}